.configuration-container {
  @apply w-full
    h-full
    relative;
}

.configuration {
  @apply absolute
    top-0
    right-0
    bottom-0
    left-0
    flex
    p-2
    border-lightgray;
}

.config-tabs {
  @apply w-full;
}

.config-tab {
  @apply w-full
    overflow-y-auto;
  height: inherit;
}
